<template>
  <div class="container">
    <h2></h2>
    <div class="text-content">
      <p>
        1.,,,
      </p>
      <p>
        2.,(>>>)
      </p>
      <p>3.7,8,,</p>
      <p>
        4.,,,,,
      </p>
      <p>5.A,2</p>
    </div>

    <h2>,:{{ gameNumber }}</h2>
    <div class="card suit" :class="firstCard.typeValue">
      <div
        class="card-content"
        :style="{ color: firstCard.color }"
        v-if="firstCard.number <= 10"
      >
        {{ firstCard.number }}
      </div>
      <div
        class="card-content"
        :style="{ color: firstCard.color }"
        v-if="firstCard.number == 11"
      >
        J
      </div>
      <div
        class="card-content"
        :style="{ color: firstCard.color }"
        v-if="firstCard.number == 12"
      >
        Q
      </div>
      <div
        class="card-content"
        :style="{ color: firstCard.color }"
        v-if="firstCard.number == 13"
      >
        K
      </div>
      <div
        class="card-content"
        :style="{ color: firstCard.color }"
        v-if="firstCard.number == 14"
      >
        A
      </div>
      <div
        class="card-content"
        :style="{ color: firstCard.color }"
        v-if="firstCard.number == 9999"
      >
        
      </div>
      <div class="prompt-text"></div>
    </div>
    <div class="action-btn-area">
      <template v-if="gameRule == 'num'">
        <div
          class="single-btn-group"
          v-for="(item, index) in numBtnList"
          :key="index"
        >
          <el-button
            type="primary"
            class="guess-btn"
            @click="guessCard(item.value)"
            >{{ item.name }}
          </el-button>
          <div class="rate-text">:{{ item.rate || 0 }}%</div>
        </div>
      </template>
      <template v-if="gameRule == 'color'">
        <div
          class="single-btn-group"
          v-for="(item, index) in colorBtnList"
          :key="index"
        >
          <el-button
            type="primary"
            class="guess-btn"
            @click="guessCard(item.value)"
            >{{ item.name }}</el-button
          >
          <div class="rate-text">:{{ item.rate || 0 }}%</div>
        </div>
      </template>
    </div>

    <div class="card suit" :class="guessCardRes.typeValue" v-if="guessCardRes">
      <div
        class="card-content"
        :style="{ color: guessCardRes.color }"
        v-if="guessCardRes.number <= 10"
      >
        {{ guessCardRes.number }}
      </div>
      <div
        class="card-content"
        :style="{ color: guessCardRes.color }"
        v-if="guessCardRes.number == 11"
      >
        J
      </div>
      <div
        class="card-content"
        :style="{ color: guessCardRes.color }"
        v-if="guessCardRes.number == 12"
      >
        Q
      </div>
      <div
        class="card-content"
        :style="{ color: guessCardRes.color }"
        v-if="guessCardRes.number == 13"
      >
        K
      </div>
      <div
        class="card-content"
        :style="{ color: guessCardRes.color }"
        v-if="guessCardRes.number == 14"
      >
        A
      </div>
      <div
        class="card-content"
        :style="{ color: guessCardRes.color }"
        v-if="guessCardRes.number == 9999"
      >
        
      </div>
      <div class="prompt-text"></div>
    </div>
    <div v-if="gameRes" class="result-font">:{{ gameRes }}</div>
  </div>
</template>

<script>
import { onMounted, reactive, toRefs } from "vue";
import { ElMessageBox } from "element-plus";
export default {
  name: "card-game",
  setup() {
    const state = reactive({
      gameNumber: 1,
      numBtnList: [
        {
          name: "",
          value: "big",
          rate: "",
        },
        {
          name: "",
          value: "small",
          rate: "",
        },
      ],
      colorBtnList: [
        {
          name: "",
          value: "red",
          rate: "",
        },
        {
          name: "",
          value: "black",
          rate: "",
        },
      ],
      actionBtnList: [
        { name: "", value: "big" },
        { name: "", value: "small" },
      ],
      actionColorBtnList: [
        { name: "red", value: "red" },
        { name: "black", value: "black" },
      ],
      actionColorVal: "red",
      actionVal: "",
      cardList: [],
      gameRule: "num", //
      firstCard: "", //
      guessCardRes: "", //
      gameRes: "", //
    });
    //
    const initCard = () => {
      const typeList = [
        {
          typeName: "",
          weight: 4,
          color: "red",
          typeValue: "hearts",
          number: 2,
        },
        {
          typeName: "",
          weight: 3,
          color: "black",
          typeValue: "spades",
          number: 4,
        },
        {
          typeName: "",
          weight: 2,
          color: "black",
          typeValue: "clubs",
        },
        {
          typeName: "",
          weight: 1,
          color: "red",
          typeValue: "diamonds",
        },
      ];
      const ghostCardList = [
        { typeName: "", number: 9999, weight: 99999, color: "black" },
        { typeName: "", number: 9999, weight: 99999, color: "red" },
      ];
      for (let i = 2; i <= 14; i++) {
        typeList.forEach((item) => {
          state.cardList.push({
            number: i,
            typeName: item.typeName,
            weight: item.weight,
            color: item.color,
            typeValue: item.typeValue,
          });
        });
      }
      state.cardList = [...state.cardList, ...ghostCardList];
      console.log("list", state.cardList);
    };
    //
    const shuffle = (list) => {
      let len = list.length;
      for (let i = len - 1; i >= 0; i--) {
        let randomIndex = Math.floor(Math.random() * i);
        let temp = list[i];
        list[i] = list[randomIndex];
        list[randomIndex] = temp;
      }
      return list;
    };
    //
    const dealCard = (list) => {
      state.gameRes = "";
      list.splice(0, 1);
      // const card = list.splice(0, 1);
      return list;
    };

    //
    const guessCard = (status) => {
      const dealCardList = dealCard(state.cardList);
      if (dealCardList.length == 0) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
        return;
      }
      state.guessCardRes = dealCardList[0]; //

      //
      if (state.gameRule == "num") {
        //7,8,,7,8
        if (state.guessCardRes.number == 7 || state.guessCardRes.number == 8) {
          showModal("");
          console.log("7/8,");
          return;
        }
        const compareResult = compareCardNumRes(
          state.firstCard,
          state.guessCardRes
        );

        if (
          status == "big" &&
          state.guessCardRes.typeName != "" &&
          state.guessCardRes.typeName != ""
        ) {
          state.gameRes = compareResult < 0 ? "" : "";
          showModal(state.gameRes);
        }
        if (
          status == "small" &&
          state.guessCardRes.typeName != "" &&
          state.guessCardRes.typeName != ""
        ) {
          state.gameRes = compareResult > 0 ? "" : "";
          showModal(state.gameRes);
        }
      }
      //,
      console.log("state.guessCardRes", state.guessCardRes);
      if (
        state.guessCardRes.typeName == "" ||
        state.guessCardRes.typeName == "" ||
        state.gameRule == "color"
      ) {
        if (state.gameRule == "num") {
          showModal("");
        } else {
          console.log("");
          changeCardColor(status);
        }
      }

    };
    //
    const resetCardStatus = (resRule) => {
      state.firstCard = state.guessCardRes;
      state.guessCardRes = "";
      state.gameRes = "";
      let rule;
      if (
        (state.firstCard.weight == "99999" &&
          state.guessCardRes.weight == "99999") ||
        resRule == "color"
      ) {
        rule = "color";
        calcColorRate();
      } else {
        rule = "num";
        calcWinRate();
      }

      state.gameRule = rule;
    };
    //
    const changeCardColor = (color) => {
      state.gameRule = "color";
      const compareResult = compareCardColor(color);
      state.gameRes = compareResult > 0 ? "" : "";
      showModal(state.gameRes);

      console.log("result", state.gameRes);
    };
    //
    const showModal = (status) => {
      setTimeout(() => {
        if (status == "") {
          ElMessageBox.confirm(",?", "", {
            confirmButtonText: ",",
            cancelButtonText: "",
          })
            .then(() => {
              console.log("");
              resetCardStatus();
            })
            .catch(() => {
              //
              resetCardStatus();
              state.gameNumber += 1;
            });
        }

        if (status == "") {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
            callback: () => {
              resetCardStatus();
              state.gameNumber += 1;
            },
          });
        }
        if (status == "") {
          ElMessageBox.alert("7", "", {
            confirmButtonText: "",
            callback: () => {
              resetCardStatus();
            },
          });
        }
        if (status == "") {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
            callback: () => {
              calcColorRate();
              resetCardStatus("color");
            },
          });
        }
      }, 1000);
    };

    //

    //
    //
    //
    const compareCardNumRes = (firstCard, secondCard) => {
      console.log("", firstCard);
      console.log("", secondCard);
      console.log("", firstCard.number - secondCard.number);
      if (firstCard.number == secondCard.number) {
        return firstCard.weight - secondCard.weight;
      }
      return firstCard.number - secondCard.number;
    };
    //
    const compareCardColor = (color) => {
      console.log("", color);
      console.log("", state.guessCardRes.color);
      if (color == state.guessCardRes.color) {
        return 1;
      } else {
        return -1;
      }
    };
    //,,
    const calcNumRate = (firstCard, secondCard) => {
      const paceList = [7, 8, 9999]; //7,8,
      if (paceList.includes(secondCard.number)) {
        return 0;
      }
      if (firstCard.number == secondCard.number) {
        return firstCard.weight - secondCard.weight;
      }
      return firstCard.number - secondCard.number;
    };
    //
    const calcColorRate = () => {
      const redWinRate = Math.round(
        (state.cardList.filter((item) => item.color == "red").length /
          state.cardList.length) *
          100
      );
      const blackWinRate = Math.round(
        (state.cardList.filter((item) => item.color == "black").length /
          state.cardList.length) *
          100
      );
      console.log("", state.cardList);
      state.colorBtnList[0].rate = redWinRate;
      state.colorBtnList[1].rate = blackWinRate;
    };
    //
    const calcWinRate = () => {
      console.log("state.firstCard.number)", state.firstCard.number);
      const rateList = state.cardList.map((item) => {
        return calcNumRate(state.firstCard, item);
      });
      const bigWinRate = Math.round(
        (rateList.filter((item) => item < 0).length / state.cardList.length) *
          100
      );
      const smallWinRate = Math.round(
        (rateList.filter((item) => item > 0).length / state.cardList.length) *
          100
      );
      state.numBtnList[0].rate = bigWinRate;
      state.numBtnList[1].rate = smallWinRate;
      console.log("", state.cardList.length);
    };
    onMounted(() => {
      initCard();
      let intCard = shuffle(state.cardList);
      console.log("stateCard", state.cardList);
      state.firstCard = dealCard(intCard)[0];
      // console.log("res", state.firstCard);
      state.gameRule = state.firstCard.weight == 99999 ? "color" : "num";
      state.gameRule == "num" ? calcWinRate() : calcColorRate();
    });
    return {
      ...toRefs(state),
      initCard,
      guessCard,
    };
  },
};
</script>

